<template>
    <div class="pos">
        <el-row style="min-width: 798px">
            <el-col :span="10" style="min-height: 100%;" class="order-list">
                <el-tabs>
                    <el-tab-pane label="点餐">
                        <el-table :data="tabdata" border show-summary style="width: 100%">
                            <el-table-column prop="goodsName" label="商品名称"></el-table-column>
                            <el-table-column prop="count" label="数量" width="70"></el-table-column>
                            <el-table-column prop="sum" label="金额" width="70"></el-table-column>
                            <el-table-column prop="" label="操作" width="100" fixed="right">
                                <template scope="scope">
                                    <el-button type="text" size="small" @click="delete1(scope.row)">删除</el-button>
                                    <el-button type="text" size="small" @click="add(scope.row)">增加</el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                        <div class="list-bottom">
                            <el-button type="warning">挂单</el-button>
                            <el-button type="danger" @click="deletelall()">删除</el-button>
                            <el-button type="success">结账</el-button>
                        </div>
                    </el-tab-pane>
                    <el-tab-pane label="挂单">
                        挂单
                    </el-tab-pane>
                    <el-tab-pane label="外卖">
                        外卖
                    </el-tab-pane>
                </el-tabs>
            </el-col>
            <el-col :span="14">
                <div id="contai">
                    <div class="often-goods">
                        <div class="title">常用商品</div>
                        <div class="often-goods-list">
                            <ul>
                                <li v-for="goods in oftenGoods" @click="add(goods)">
                                    <span>{{goods.goodsName}}</span>
                                    <span class="o-price">￥{{goods.price}}元</span>
                                </li>
                            </ul>
                        </div>
                    </div>

                    <div class="goods-type">
                        <el-tabs>
                            <el-tab-pane label="汉堡">
                                <ul class='cookList'>
                                    <li v-for="goods in type0Goods"  @click="add(goods)">
                                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                        <span class="foodName">{{goods.goodsName}}</span>
                                        <span class="foodPrice">￥{{goods.price}}元</span>
                                    </li>
                                </ul>
                            </el-tab-pane>
                            <el-tab-pane label="小食">
                                <ul class='cookList'>
                                    <li v-for="goods in type1Goods"  @click="add(goods)">
                                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                        <span class="foodName">{{goods.goodsName}}</span>
                                        <span class="foodPrice">￥{{goods.price}}元</span>
                                    </li>
                                </ul>
                            </el-tab-pane>
                            <el-tab-pane label="饮料">
                                <ul class='cookList'>
                                    <li v-for="goods in type2Goods" @click="add(goods)">
                                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                        <span class="foodName">{{goods.goodsName}}</span>
                                        <span class="foodPrice">￥{{goods.price}}元</span>
                                    </li>
                                </ul>
                            </el-tab-pane>
                            <el-tab-pane label="套餐">
                                <ul class='cookList'>
                                    <li v-for="goods in type3Goods">
                                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                        <span class="foodName">{{goods.goodsName}}</span>
                                        <span class="foodPrice">￥{{goods.price}}元</span>
                                    </li>
                                </ul>
                            </el-tab-pane>

                        </el-tabs>
                    </div>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import axios from 'axios';
export default {
    name: 'pos',
    data() {
        return {
            type0Goods: [],
            type1Goods: [],
            type2Goods: [],
            type3Goods: [],
            oftenGoods: [],          
            tabdata: []
        }
    },
    methods:{
        //新增商品
        add(goods){
            let hasgoods = false;
            //判断候选区有没有被点击的商品
            for(let i = 0; i<this.tabdata.length; i++){
                if(this.tabdata[i].goodsId == goods.goodsId){
                    hasgoods = true;
                }           
            }
            //如果有就数量加1否则追加一个新的商品信息数组
            console.log(hasgoods)
            if(hasgoods){
                 let arr = this.tabdata.filter(item =>item.goodsId == goods.goodsId);                
                 arr[0].count++;
                 arr[0].sum = arr[0].price*arr[0].count
            }else{
                let newGoods={
                    goodsId: goods.goodsId,
                    goodsName: goods.goodsName,
                    price: goods.price,
                    sum: goods.price,
                    count:1
                }
                console.log(newGoods)
                this.tabdata.push(newGoods)
            }
        },
        delete1(goods){
            this.tabdata = this.tabdata.filter(item => item.goodsId != goods.goodsId)
        },
        deletelall(){
            this.tabdata = []
        }


    },
    created: function() {
        axios.get('http://jspang.com/DemoApi/oftenGoods.php')
            .then(result => {
                console.log(result.data)
                this.oftenGoods = result.data
            })
            .catch(() => {
                alert("网络不通")
            })
        axios.get('http://jspang.com/DemoApi/typeGoods.php')
            .then(result => {
                console.log(result.data[0])
                this.type0Goods = result.data[0]
                this.type1Goods = result.data[1]
                this.type2Goods = result.data[2]
                this.type3Goods = result.data[3]
            })
            .catch(() => {
                alert("网络不通")
            })
    }
}
</script>

<style scoped>
.order-list {
    height: 100vh;
}

.list-bottom {
    margin-top: 20px;
}

.title {
    height: 20px;
    border-bottom: 1px solid #D3DCE6;
    background-color: #F9FAFC;
    padding: 10px;
}


/* 热门商品区 */
#contai{
    width: 100%;
    height: 100vh;
    border-left: 1px solid #20a0ff;
}
.often-goods-list ul li {
    list-style: none;
    float: left;
    border: 1px solid #E5E9F2;
    padding: 10px;
    margin: 5px;
    background-color: #fff;
}

.often-goods-list ul:after {
    content: "";
    display: block;
    clear: both;
}

.o-price {
    color: #58B7FF;
}


/* 商品选择区 */

.cookList li {
    list-style: none;
    width: 177px;
    border: 1px solid #E5E9F2;
    height: auot;
    overflow: hidden;
    background-color: #fff;
    padding: 2px;
    float: left;
    margin: 2px;
}

.cookList li span {

    display: block;
    float: left;
}

.foodImg {
    width: 40%;
    vertical-align: middle;
}

.foodName {
    font-size: 18px;
    padding-left: 10px;
    color: brown;
    width: 54%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.foodPrice {
    font-size: 16px;
    padding-left: 10px;
    padding-top: 10px;
}
</style>



