import Vue from 'vue'
import Router from 'vue-router'
import Pos from '@/components/page/Pos'
import leftnav from '@/components/common/leftnav'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'pos',
      component: Pos
    },
    {
      path: './leftnav',
      name: 'lnav',
      component: leftnav
    }
  ]
})



